import React, { useState } from "react";
import disableScroll from "disable-scroll";

function Navigation() {
  const [navbar, setNavbar] = useState(false);
  const [isDisabled, setDisabled] = useState(true);
  const cheackBG = () => {
    if (window.scrollY >= 90) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  };
  window.addEventListener("scroll", cheackBG);

  function noScroll() {
    setDisabled(!isDisabled);
    if (isDisabled == true) {
      disableScroll.on();
    } else {
      disableScroll.off();
    }
  }
  return (
    <div className="toolbar">
      <div className="row">
        <div className="col">
          <h1>Тасеевская ЦКС</h1>
          <h6>Официальный сайт</h6>
        </div>
        <div className="col">
          <div className="social_icon">
            <a href="">
              <i className="fab fa-facebook-f"></i>
            </a>
            <a href="">
              <i className="fab fa-twitter"></i>
            </a>
            <a href="">
              <i className="fab fa-instagram"></i>
            </a>
            <a href="">
              <i className="fas fa-envelope"></i>
            </a>
          </div>
        </div>
      </div>
      <nav
        className={
          navbar
            ? "navbar fixed-top navbar-expand-lg navbar-dark bg-light bor"
            : "navbar navbar-expand-lg navbar-dark bor"
        }
      >
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon" onClick={noScroll}></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto ml-auto">
            <li className="nav-item active">
              <a className="nav-link" href="#">
                Главная <span className="sr-only">(current)</span>
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#">
                Новости
              </a>
            </li>
            <li className="nav-item dropdown">
              <a
                className="nav-link dropdown-toggle"
                href="#"
                id="navbarDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                Визитка
              </a>
              <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                <div className="row">
                  <div className="col">
                    <a href="">Основные сведения</a>
                  </div>
                  <div className="col">
                    <a href="">НПБ</a>
                  </div>
                  <div className="col">
                    <a href="">На карте</a>
                  </div>
                </div>
                <div className="row">
                  <div className="col">
                    <a href="">Структура</a>
                  </div>
                  <div className="col">
                    <a href="">ФХД</a>
                  </div>
                </div>
                <div className="row">
                  <div className="col">
                    <a href="">
                      Информация о материально-техническом обеспечении
                    </a>
                  </div>
                  <div className="col">
                    <a href="">Плпн работы</a>
                  </div>
                </div>
                <div className="row">
                  <div className="col">
                    <div className="card">
                      <img src="..." className="card-img-top" alt="..." />
                      <div className="card-body">
                        <h5 className="card-title">Это</h5>
                        <p className="card-text">
                          Some quick example text to build on the card title and
                          make up the bulk of the card's content.
                        </p>
                        <a href="#" className="btn btn-primary">
                          Переход куда-нибудь
                        </a>
                      </div>
                    </div>
                  </div>
                  <div className="col">
                    <div className="card">
                      <img src="..." className="card-img-top" alt="..." />
                      <div className="card-body">
                        <h5 className="card-title">можно</h5>
                        <p className="card-text">
                          Some quick example text to build on the card title and
                          make up the bulk of the card's content.
                        </p>
                        <a href="#" className="btn btn-primary">
                          Переход куда-нибудь
                        </a>
                      </div>
                    </div>
                  </div>
                  <div className="col">
                    <div className="card">
                      <img src="..." className="card-img-top" alt="..." />
                      <div className="card-body">
                        <h5 className="card-title">так</h5>
                        <p className="card-text">
                          Some quick example text to build on the card title and
                          make up the bulk of the card's content.
                        </p>
                        <a href="#" className="btn btn-primary">
                          Переход куда-нибудь
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#">
                Услуги
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#">
                Конкурсы
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#">
                Независимая оценка
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#">
                Обратная связь
              </a>
            </li>
          </ul>
        </div>
      </nav>
    </div>
  );
}
export default Navigation;
